import './DefaultNavbar.scss';

import { Link } from 'react-router-dom';

/**
 * Default Navbar
 */
const DefaultNavbar = () => {
  return (
    <nav className="navbar">
      <ul>
        <li>
          <Link to="/">Accueil</Link>
        </li>
        <li>
          <Link to="/">Profil</Link>
        </li>
        <li>
          <Link to="/">Rglage</Link>
        </li>
        <li>
          <Link to="/">Communaut</Link>
        </li>
      </ul>
    </nav>
  );
};

export default DefaultNavbar;
